@mixin burger-w-text($burger-width: 52px, $burger-color: black, $burger-font: Arial) {
	width: $burger-width;
	cursor: pointer;
	height: $burger-width * .69;
	position: relative;
	color: $burger-color;
	background-color: transparent;
	border: none;
	padding: 0;
	transition: all .35s ease;
	text-transform: uppercase;
	outline: none;
	&::before,
	&::after {
		content: "";
		display: block;
		position: absolute;
		width: 90%;
		background-color: $burger-color;
		left: 50%;
		transform-origin: 50% 50%;
		//height: $burger-width * .085;
		height: 2px;
		transition: all .35s ease;
		transform: translateX(-50%);
		margin-left: -1px;
	}
	&::before {
		top: 0;
		bottom: auto;
	}
	&::after {
		bottom: 0;
		top: auto;
		@include retina {
			bottom: 2px;
		}
	}
	span {
		position: absolute;
		top: 50%;
		left: 0;
		font-family: $burger-font;
		width: 100%;
		text-align: center;
		transform: translateY(-50%);
		transition: all .45s ease;
		color: $burger-color;
		font-size: $burger-width / 3;
		font-weight: 700;
		line-height: $burger-width * .6;
		vertical-align: middle;
		
		&::before,
		&::after {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			background-color: $burger-color;
			left: 50%;
			transform-origin: 50% 50%;
			//height: $burger-width * .085;
			height: 2px;
			transition: all .35s ease;
			transform: translateX(-50%);
			margin-left: -1px;
		}
		&::before {
			top: 3px;
			bottom: auto;
		}
		&::after {
			bottom: 2px;
			@include retina {
				bottom: 4px;
			}
			top: auto;
		}
		
	}
	&:hover {
		&::before,
		&::after {
			transform: translateX(-35%);
		}
		span {
			transform: translateX(-10%) translateY(-50%)
		}
	}
	&_open {
		&::before,
		&::after {
			width: 100%;
		}
		span {
			opacity: 0;
		}
		&::before {
			transform: rotate(225deg) translateY(-50%) translateX(0);
			top: 45%;
			left: 0;
		}
		&::after {
			transform: rotate(-225deg) translateY(50%) translateX(0);
			bottom: 45%;
			left: 0;
		}
		&:hover {
			&::before {
				transform: rotate(225deg) translateY(-50%) translateX(0);
			}
			&::after {
				transform: rotate(-225deg) translateY(50%) translateX(0);
			}
			transform: scale(.85);
		}
	}
	// уберем hover на маленьких экранах, где точно touch
	@media screen and ("max-width: 600px") {
		&:hover {
			&::before,
			&::after {
				transform: translateX(-50%);
			}
			span {
				transform: translateY(-50%)
			}
		}
		&_open:hover {
			&::before {
				transform: rotate(225deg) translateY(-50%) translateX(0);
			}
			&::after {
				transform: rotate(-225deg) translateY(50%) translateX(0);
			}
			transform: scale(.85);
		}
	}
	@content;
}
@mixin burger($burger-width: 36px, $burger-color: #000, $burger-thickness: 4px, $burger-radius: $burger-thickness / 2) {
	position: relative;
	width: $burger-width;
	height: $burger-width * .65;
	cursor: pointer;
	background-color: transparent;
	transition: all .45s ease;
	&:before,
	&:after {
		content: "";
		background: $burger-color;
		backface-visibility: hidden;
		height: $burger-thickness;
		border-radius: $burger-radius;
		left: 0;
		width: 100%;
		position: absolute;
		transform-origin: 50% 50%;
		transition: transform .45s ease, background-color .45s ease, box-shadow .45s ease;
	}
	&:before {
		box-shadow: $burger-color 0 ($burger-width * .65 / 2 - $burger-thickness / 2) 0 0;
		top: 0;
	}
	&:after {
		bottom: 0;
	}
	&:hover {
		&::before,
		&::after {
			transform: translateX(10%);
		}
		&::before {
			box-shadow: $burger-color (-$burger-width * .2) ($burger-width * .65 / 2 - $burger-thickness / 2) 0 0;
		}
	}
	@media screen and ("max-width: 600px") {
		&:hover {
			&::before,
			&::after {
				transform: translateX(0);
			}
			&::before {
				box-shadow: $burger-color 0 ($burger-width * .65 / 2 - $burger-thickness / 2) 0 0;
			}
		}
	}
	&_open {
		&:before {
			box-shadow: transparent 0 0 0 0;
			transform: translate(0, ($burger-width * .65 / 2 - $burger-thickness / 2)) rotate(225deg);
		}
		&:after {
			transform: translate(0, -($burger-width * .65 / 2 - $burger-thickness / 2)) rotate(-225deg);
		}
		&:hover {
			transform: scale(.9);
			&:before {
				box-shadow: transparent 0 0 0 0;
				transform: translate(0, ($burger-width * .65 / 2 - $burger-thickness / 2)) rotate(225deg);
			}
			&:after {
				transform: translate(0, -($burger-width * .65 / 2 - $burger-thickness / 2)) rotate(-225deg);
			}
		}
	}
	@content;
}
@mixin justify-block($block-width: 30%, $vertical-align: top, $font-size: 1rem, $line-height: 1.4, $margin-bottom: 0) {
	text-align: justify;
	font-size: 1px;
	line-height: 0;
	& > * {
		display: inline-block;
		width: $block-width;
		vertical-align: $vertical-align;
		font-size: $font-size;
		line-height: $line-height;
		margin-bottom: $margin-bottom;
	}
	&:after {
		width: 100% !important;
		height: 0 !important;
		visibility: hidden !important;
		overflow: hidden !important;
		content: '' !important;
		display: inline-block !important;
	}
}

@mixin full_position {
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
}

@mixin flex_columns($width: 50%, $gap: 15px) {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: flex-start;
	margin-left: -$gap;
	margin-right: -$gap;
	&>div {
		max-width: $width;
		margin-left: $gap;
		margin-right: $gap;
	}
}

@mixin inputs_text {
	input[type="text"],
	input[type="url"],
	input[type="tel"],
	input[type="number"],
	input[type="email"],
	input[type="color"],
	input[type="password"] {
		border: 1px solid transparent;
		@content;
		&.error {
			border-color: red;
		}
	}
}

@mixin inputs_submit {
	input[type="submit"],
	input[type="button"] {
		@content;
	}
}

@mixin sides_padding($width: 20px) {
	padding-left: $width;
	padding-right: $width;
}
@mixin sides_margin($width: 20px) {
	margin-left: $width;
	margin-right: $width;
}

@mixin visible {
	visibility: visible;
	opacity: 1;
}

@mixin hidden {
	visibility: hidden;
	opacity: 0;
}

